/* RESET */
*, *:before, *:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  font-size: 62.5%;
  font-family: 'Open Sans'/*rtl:Sahel*/
}
html[dir], [dir] body, [dir] h1, [dir] h2, [dir] h3, [dir] h4, [dir] h5, [dir] h6, [dir] p, [dir] ol, [dir] ul, [dir] menu, [dir] section {
  margin: 0;
  padding: 0;
}
/* Resume */
[dir] .resume {
    margin: 20px 5%;
}
.resume a {
    color: #473B38;
}
.resume a:visited {
    color: #946456;
}
.name {
    font-size: 5rem;
    color: #283330;
}
#profession {
    font-size: 1.4rem;
    color: #283330;
}
.job-section {
    width: 60%;
}
.listing-title, #skills {
    font-size: 2.2rem;
    font-weight: 600;
    color: #384743;
}
[dir] .listing-title, [dir] #skills {
    margin-top: 15px;
}
.job-title {
    font-size: 1.8rem;
    line-height: 1.6;
    font-weight: 700;
    color: #42544F;
}
.job-link {
    font-size: 1.6rem;
    line-height: 1.5;
    font-weight: 600;
}
[dir] .job-link {
    padding-bottom: 5px;
}
.job-duration {
    font-size: 1.4rem;
    font-style: italic;
    color: #867b86;
    font-weight: 600;
    line-height: 1.5;
}
[dir] .job-duration {
    padding-bottom: 10px;
}
[dir] .job-entry {
    padding-bottom: 15px;
}
[dir] .job-heading {
    margin-top: 10px;
}
.job-desc {
    line-height: 1.5;
    font-family: Lato;
    font-size: 1.6rem;
    color: #283330;
}
[dir] .job-desc {
    padding-bottom: 5px;
}
[dir] .job-desc ul:not(:first-child) {
    margin-top: 10px;
}
.job-desc ul {
    font-weight: 400;
    line-height: 1.6;
}
[dir=ltr] .job-desc ul {
  margin-left: 20px;
}
[dir=rtl] .job-desc ul {
    margin-right: 20px;
}
.job-tech {
    font-family: Lato;
    font-size: 1.4rem;
    font-style: italic;
    font-weight: 700;
    color: #42544F;
}
/* skill svg */
.skill-set {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: start;
    width: 45%;
}
[dir] .skill-set {
    margin: 20px 0;
}
.skill-title {
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
    font-family: Lato;
    font-size: 1.8rem;
    color: #283330;
}
.skill-svg svg {
    width: 30%;
    height: 30%
}
@media (max-width: 991.98px) {
    .job-section {
        width: 80%
    }
}
@media (max-width: 767.98px) {
    .job-section {
        width: 100%
    }
}
@media (max-width: 575.98px) {
    .job-section {
        width: 100%;
    }
    .skill-set {
        width: 100%;
    }
}
/* MENU */
.menu {
  width: 15%;
  position: fixed;
  z-index: 10;

}
.menu ul {
  width: 80%;
  -webkit-transform: translate(0,-250px);
      -ms-transform: translate(0,-250px);
  transition: opacity 0.3s cubic-bezier(.27,.72,.05,1.24), -webkit-transform 0.3s 0.1s cubic-bezier(.27,.72,.05,1.24);
  -o-transition: opacity 0.3s cubic-bezier(.27,.72,.05,1.24), transform 0.3s 0.1s cubic-bezier(.27,.72,.05,1.24);
  transition: opacity 0.3s cubic-bezier(.27,.72,.05,1.24), transform 0.3s 0.1s cubic-bezier(.27,.72,.05,1.24);
  transition: opacity 0.3s cubic-bezier(.27,.72,.05,1.24), transform 0.3s 0.1s cubic-bezier(.27,.72,.05,1.24), -webkit-transform 0.3s 0.1s cubic-bezier(.27,.72,.05,1.24);
  line-height: 2;
  opacity: 0;
}
[dir] .menu ul {
          transform: translate(0,-250px);
  -webkit-transition: opacity 0.3s cubic-bezier(.27,.72,.05,1.24), -webkit-transform 0.3s 0.1s cubic-bezier(.27,.72,.05,1.24);
  margin: 0 auto;
  padding-top: 20px;
}
.menu li {
  list-style-type: none;
}
.menu li a {
  text-decoration: none;
  color: #ababab;
  font-weight: 600;
  font-size: 1rem;
}
.menu li a:hover {
  color: #dfdfdf;
}
[dir] .menu li a:hover {
  cursor: pointer;
}
.menu.dark li a, .menu.dark a:hover {
  color: #1b1b1b;
}
.menu li {
  -o-transition: all 0.3s cubic-bezier(0.46,0.03,.52,0.96);
  transition: all 0.3s cubic-bezier(0.46,0.03,.52,0.96);
}
[dir] .menu li {
  -webkit-transition: all 0.3s cubic-bezier(0.46,0.03,.52,0.96);
}
.menu.show ul {
  opacity: 1;
  -webkit-transform: translate(0,150px);
      -ms-transform: translate(0,150px);
}
[dir] .menu.show ul {
          transform: translate(0,150px);
}
#lang-switch {
  position: fixed;
  bottom: 20px;
  font-weight: 400;
  font-size: 1.6rem;
  color: #ababab;
}
[dir] #lang-switch {
  background-color: transparent;
  padding: 10px 20px;
  margin-top: 20px;
  border-radius: 10px;
  border: 4px solid #cbcbcb;
}
[dir=ltr] #lang-switch {
  margin-left: 40px;
  font-family: "Vazir"/*rtl:Lato*/;
}
[dir=rtl] #lang-switch {
  margin-right: 40px;
  font-family: Lato;
}
#lang-switch.dark {
  color: #4e4e4e;
}
[dir] #lang-switch.dark {
  border-color: #868686;
}
#credit {
  font-family: 'Open Sans';
  font-size: 1.2rem;
  position: absolute;
  bottom: 0;
}
[dir] #credit {
  padding: 10px 20px;
}
[dir=ltr] #credit {
  right: 0;
}
[dir=rtl] #credit {
  left: 0;
}
/* ENTRY */
.entry {
  min-height: 100vh;
}
[dir] .entry {
  padding-top: 20px;
}
[dir=ltr] .entry {
  padding-left: 15%;
  padding-right: 5%;
}
[dir=rtl] .entry {
  padding-right: 15%;
  padding-left: 5%;
}
.entry.flex-col {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
[dir] .entry.flex-col {
  padding-top: 0;
}
.entry.flex-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.entry-copy, .entry-images {
  width: 80%;
}
[dir] .entry-copy, [dir] .entry-images {
  margin: 0 auto;
  padding:  20px 10px;
}
.entry-title {
  font-variant-caps: small-caps;
  color: #6c5a5a;
  -o-transition: font-size 0.1s;
  transition: font-size 0.1s;
}
[dir] .entry-title {
  -webkit-transition: font-size 0.1s;
}
.entry-intro {
  -o-transition: font-size 0.1s ;
  transition: font-size 0.1s ;
  color: #4D5D66;
  display: block;
}
[dir] .entry-intro {
  -webkit-transition: font-size 0.1s ;
  padding-top: 10px;
  margin-top: 0;
}
.entry-less {
  display: none;
}
.entry-desc {
  display: none;

  line-height: 1.8;
  z-index: -10;
  transition: display 0.5s, z-index 0.5s, -webkit-transform 0.5s;
  -o-transition: display 0.5s, transform 0.5s, z-index 0.5s;
  transition: display 0.5s, transform 0.5s, z-index 0.5s;
  transition: display 0.5s, transform 0.5s, z-index 0.5s, -webkit-transform 0.5s;
  color: #535353;
}
[dir] .entry-desc {
  padding: 10px 20px;
  -webkit-transition: display 0.5s, z-index 0.5s, -webkit-transform 0.5s;
}
[dir=ltr] .entry-desc {
  -webkit-transform: translate(-100px);
  -ms-transform: translate(-100px);
  transform: translate(-100px);
}
[dir=rtl] .entry-desc {
  -webkit-transform: translate(100px);
      -ms-transform: translate(100px);
          transform: translate(100px);
}
.entry-contact {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #535353;
}
[dir] .entry-contact {
  padding: 10px 20px;
}
.entry-desc.show {
  display: block;
  -webkit-transform: none;
      -ms-transform: none;
  z-index: 10;
}
[dir] .entry-desc.show {
          transform: none;
}
[dir=ltr] .entry-desc ul {
  padding-left: 20px;
}
[dir=rtl] .entry-desc ul {
  padding-right: 20px;
}
.light .entry-title {
  color: /*#dab8b8*/ #C46D6E;
}
.light .entry-intro, .light .entry-desc,
.light .entry-more, .light .entry-less {
  color: #b4b4b4;
}
.more .entry-title {
  font-size: 2rem
}
.more .entry-desc {
  z-index: 10;
  -webkit-transform: none;
      -ms-transform: none;
  display: block
}
[dir] .more .entry-desc {
          transform: none
}
.more .entry-intro {
    font-size: 1rem;
    display: none;
}
.more .entry-less {
  display: inline-block;
}
[dir] .more .entry-less {
  margin-top: 10px;
}
[dir=ltr] .more .entry-less {
  margin-left:  20px;
}
[dir=rtl] .more .entry-less {
  margin-right:  20px;
}
.entry-more, .entry-less {
  font-weight: 700;
  z-index: 0;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
}
[dir] .entry-more, [dir] .entry-less {
  cursor: pointer;
  border: 2px #626262 solid;
  padding: 10px 20px;
  margin-top: 30px;
  text-align: center;

  border-radius: 6px;
          transform: translateY(0);
}
/* ENTRY & CONTENT */
#content .entry-more, #content .entry-less {
  font-size: 1.1rem;
}
[dir] #content .entry-more, [dir] #content .entry-less {
  text-align: center;
  border-radius: 6px;
}
.more .entry-more, #content .more .entry-more {
  z-index: -10;
  -webkit-transform: translateY(-100px);
      -ms-transform: translateY(-100px);
  display: none
}
[dir] .more .entry-more, [dir] #content .more .entry-more {
          transform: translateY(-100px)
}
.entry-more, #content .entry-more {
  display: inline-block;
  transition: z-index 0.5s, display 0.5s, -webkit-transform 0.5s;
  -o-transition: transform 0.5s, z-index 0.5s, display 0.5s;
  transition: transform 0.5s, z-index 0.5s, display 0.5s;
  transition: transform 0.5s, z-index 0.5s, display 0.5s, -webkit-transform 0.5s;
}
[dir] .entry-more, [dir] #content .entry-more {
  margin-top: 30px;
  -webkit-transition: z-index 0.5s, display 0.5s, -webkit-transform 0.5s;
}
